.main {
  display: flex;
  flex-wrap: wrap;
  width: 1000px;
  justify-content: center;

  &__card {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 200px;
    border: 1px solid #f3f3f3;
    border-radius: 5px;
    padding: 32px 16px 16px;
    margin: 0 25px 50px;
  }
}
